.eqftp {
  background-color: $c04;
  position: absolute;
  display: flex;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: stretch;
  flex-wrap: wrap;
  overflow: hidden;
  height: 100%;
  width: 360px;
  &-toolbar {
    &__icon {
      padding-top: 1px;
      padding-bottom: 1px;
      padding-left: 1px;
      padding-right: 1px;
      background-image: url(images/toolbar__icon.svg);
      background-size: contain;
      border-radius: 2px;
      box-sizing: border-box;
      height: 26px;
      width: 26px;
      &:hover {
        background-color: $c04;
      }
      &:active {
        background-color: $c05;
      }
    }
  }
  &-header {
    background-color: $c07;
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    width: 100%;
    z-index: 300;
    &__title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: $i12;
      max-height: $i12;
      height: $i12;
      width: 100%;
      & .eqftp__title {
        width: 100%;
      }
      & .eqftp__button {
        color: $c09;
      }
    }
    &__search {
      padding-top: 6px;
      padding-bottom: 6px;
      padding-left: 6px;
      padding-right: 6px;
      box-sizing: border-box;
      display: none;
      justify-content: center;
      align-items: center;
      animation-name: fadeIn;
      animation-duration: 0.2s;
      animation-direction: alternate;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      &_active {
        display: flex;
      }
      &Holder {
        background-color: #fafafa;
        border-radius: 2px;
        display: flex;
        justify-content: space-between;
        align-content: flex-start;
        align-items: stretch;
        flex-wrap: wrap;
        width: 100%;
        & .eqftp__inputText {
          width: calc(100% - 36px - 36px) !important;
        }
      }
    }
    &__dropdown {
      padding-top: 6px;
      padding-bottom: 6px;
      border-top: 1px solid $c11;
      box-sizing: border-box;
      animation-name: fadeIn;
      animation-duration: 0.2s;
      animation-direction: alternate;
      position: relative;
      display: none;
      max-height: 480px;
      width: 100%;
      &_active {
        display: flex;
        justify-content: flex-start;
        align-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
      }
      &List {
        width: 100%;
      }
      &Item {
        padding-left: $i9;
        padding-right: $i3;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
        align-content: center;
        align-items: center;
        flex-wrap: wrap;
        transition: background-color 0.2s, border 0.2s;
        cursor: pointer;
        min-height: $i12;
        max-height: $i12;
        height: $i12;
        width: 100%;
        &:hover {
          background-color: $c09;
          & .eqftp__titleNowrap {
            &+* {
              border-left: 1px solid $c12;
            }
          }
        }
        & .eqftp__titleNowrap {
          &+* {
            margin-left: $i1;
            padding-left: $i1;
            border-left: 1px solid $c09;
            line-height: 10px;
          }
        }
      }
    }
    &__navigation {
      display: flex;
      justify-content: space-between;
      align-content: center;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
      &Tab {
        padding-left: $i3;
        padding-right: $i3;
        font-size: 20px;
        color: $c09;
        border-bottom: 2px solid transparent;
        transition: color 0.2s, border-bottom-color 0.2s;
        display: flex;
        justify-content: center;
        align-items: center;
        box-sizing: border-box;
        cursor: pointer;
        min-height: $i12;
        max-height: $i12;
        height: $i12;
        width: 25%;
        &_active,
        &:hover {
          color: $c01;
          border-bottom-color: $c01;
        }
      }
    }
  }
  &-connectionsSettings {
    display: none;
  }
  &-content {
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    max-height: calc(100% - 122px);
    height: 100%;
    width: 100%;
    z-index: 100;
    &__page {
      animation-name: fadeInRight;
      animation-duration: 0.3s;
      display: none;
      justify-content: center;
      position: relative;
      height: 100%;
      width: 100%;
      &_blank {
        align-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
      }
      &_file-tree {}
      &_queue {}
      &_connections {}
      &_settings {}
      &_active {
        display: flex;
      }
    }
  }
  &-footer {
    background-color: $c08;
    animation-name: fadeIn;
    animation-duration: 0.3s;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    overflow: hidden;
    max-height: 100%;
    height: 26px;
    width: 100%;
    z-index: 500;
    & .eqftp__buttonCube_footer {
      transition: transform 0.2s;
      &-rotate {
        transform: rotate(180deg);
      }
    }
    &_active {
      height: auto;
    }
    &__list {
      position: relative;
      display: flex;
      align-content: flex-start;
      align-items: center;
      flex-wrap: wrap;
      max-width: calc(100% - 26px);
      width: 100%;
      &_active {
        padding-top: $i2;
        max-height: 500px;
      }
      &Item {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        min-height: 26px;
        height: 26px;
        width: 100%;
        &_active {
          align-items: flex-start;
          height: auto;
          &+.eqftp-footer__listItem {
            margin-top: $i1;
          }
        }
        &_icon {
          display: flex;
          justify-content: center;
          min-width: 26px;
          max-width: 26px;
          width: 26px;
          & .material-icons {
            font-size: 16px;
          }
        }
        &_date {
          opacity: 0.4;
          min-width: 40px;
          max-width: 40px;
          width: 40px;
        }
        &_info {
          margin-left: $i2;
          min-width: 0px;
          max-width: 100%;
        }
      }
    }
  }
  &__row {
    display: flex;
    align-items: stretch;
    max-width: 100%;
    width: 100%;
    &_between {
      justify-content: space-between;
    }
    &_center {
      justify-content: center;
    }
    &_start {
      justify-content: flex-start;
    }
    &_end {
      justify-content: flex-end;
    }
  }
  &__col {
    display: flex;
    align-items: center;
    max-width: 100%;
    &_short {
      width: 33.3%;
    }
    &_half {
      width: 50%;
    }
    &_long {
      width: 66.6%;
    }
    &_full {
      width: 100%;
    }
    &_fix {
      width: 150px;
    }
    &_end {
      justify-content: flex-end;
    }
  }
  &__input {
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 0;
    padding-right: 0;
    background-color: transparent;
    border: none;
    box-sizing: border-box;
    display: flex;
    width: 100%;
    &Text {
      @include font($font-family, 14px, 300, normal, $c03, left);
      justify-content: flex-start;
      align-items: center;
      &_small {
        font-size: 12px;
      }
      &_medium {
        font-size: 14px;
      }
      &_big {
        font-size: 16px;
      }
    }
    &_short {
      width: 33.3%;
    }
    &_half {
      width: 50%;
    }
    &_long {
      width: 66.6%;
    }
    &_full {
      width: 100%;
    }
  }
  &__button {
    @include font($font-family, 16px, 300, normal, $c10, center, normal, uppercase);
    box-sizing: border-box;
    transition: color 0.2s, background-color 0.2s;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
    &:hover {}
    &_border {
      border-radius: $i2;
      border-width: 1px;
      border-style: solid;
      border-color: $c03;
    }
    &_small {
      margin-top: $i2;
      padding-top: $i1 / 2;
      padding-bottom: $i1 / 2;
      padding-left: $i1 * 2;
      padding-right: $i1 * 2;
      font-size: 12px;
      border-radius: $i1;
      & .material-icons {
        margin-bottom: $i2;
        font-size: 14px;
      }
    }
    &_medium {
      margin-top: $i4;
      padding-top: $i2 / 2;
      padding-bottom: $i2 / 2;
      padding-left: $i2 * 2;
      padding-right: $i2 * 2;
      font-size: 14px;
      border-radius: $i2;
      & .material-icons {
        margin-bottom: $i4;
        font-size: 20px;
      }
    }
    &_big {
      margin-top: $i6;
      padding-top: $i3 / 2;
      padding-bottom: $i3 / 2;
      padding-left: $i3 * 2;
      padding-right: $i3 * 2;
      font-size: 16px;
      border-radius: $i3;
      & .material-icons {
        margin-bottom: $i6;
        font-size: 26px;
      }
    }
    &Cube {
      min-height: 48px;
      max-height: 48px;
      height: 48px;
      min-width: 48px;
      max-width: 48px;
      width: 48px;
      &_footer {
        position: absolute;
        bottom: 0;
        right: 0;
        min-height: 26px;
        max-height: 26px;
        height: 26px;
        min-width: 26px;
        max-width: 26px;
        width: 26px;
      }
      &_small {
        min-height: 36px;
        max-height: 36px;
        height: 36px;
        min-width: 36px;
        max-width: 36px;
        width: 36px;
      }
      &_medium {
        min-height: 48px;
        max-height: 48px;
        height: 48px;
        min-width: 48px;
        max-width: 48px;
        width: 48px;
      }
      &_big {
        min-height: 60px;
        max-height: 60px;
        height: 60px;
        min-width: 60px;
        max-width: 60px;
        width: 60px;
      }
    }
  }
  &__hello {
    margin-top: 40px;
    margin-bottom: 40px;
    @include font($font-family, 20px, 900, normal, $c07, center, normal, uppercase);
    width: 100%;
    span {
      font-size: 40px;
    }
  }
  &__title {
    font-family: $font-family;
    font-weight: 300;
    font-style: normal;
    color: $c01;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    max-width: 100%;
    &_tiny {
      font-size: 12px;
    }
    &_small {
      font-size: 16px;
    }
    &_medium {
      font-size: 20px;
    }
    &_big {
      font-size: $i6;
    }
    &_black {
      color: $c00;
    }
    &_white {
      color: $c01;
    }
    &_gray {
      color: $c02;
    }
    &Nowrap {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      max-width: 100%;
      &_half {
        max-width: 50%;
      }
    }
  }
  &__close {
    font-size: $i6;
    color: $c09;
    transition: color 0.2s;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    min-height: $i12;
    max-height: $i12;
    height: $i12;
    min-width: $i12;
    max-width: $i12;
    width: $i12;
    &:hover {
      color: $c01;
    }
  }
  &-fileTree {
    margin-top: $i2;
    margin-bottom: $i2;
    height: 100%;
    width: 100%;
    &__item {
      padding-left: $i1;
      padding-right: $i1;
      position: relative;
      box-sizing: border-box;
      transition: background-color 0.1s;
      cursor: default;
      display: flex;
      max-width: 100%;
      width: 100%;
      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
      }
      &_file {
        align-items: center;
        min-height: $i6;
        max-height: $i6;
        height: $i6;
      }
      &_folder {
        justify-content: flex-start;
        align-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        width: 100%;
      }
      &Element {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 100%;
        width: 100%;
      }
      &Info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-width: 0px;
        width: calc(100% - 100px);
      }
      &Icon {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: $i6;
        max-height: $i6;
        height: $i6;
        min-width: $i6;
        max-width: $i6;
        width: $i6;
        & .material-icons {
          font-size: 16px;
          color: $c09;
        }
      }
      &Name {
        min-width: 0px;
        max-width: 100%;
      }
      &Ext {
        padding-left: $i1;
        opacity: 0.4;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        min-width: 0px;
        max-width: 30%;
      }
      &Size {
        padding-left: $i1;
        font-style: italic;
        opacity: 0.4;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        min-width: 0px;
        max-width: 70%;
      }
      &Date {
        padding-left: $i1;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        min-width: 100px;
        max-width: 100px;
        width: 100px;
      }
      &Wrap {
        padding-left: $i1;
        box-sizing: border-box;
        max-width: 100%;
        width: 100%;
      }
      &Children {
        padding-left: $i2;
        box-sizing: border-box;
        display: none;
        width: 100%;
        & .eqftp-fileTree__item {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }
  &-connections {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    height: 100%;
    width: 100%;
    &__connection {
      padding-top: $i2;
      padding-bottom: $i2;
      padding-left: $i2;
      padding-right: $i2;
      @include font($font-family, 12.4px, 400, normal, $c09, left);
      background-color: $c05;
      border-top-width: 1px;
      border-top-style: solid;
      border-top-color: $c03;
      transition: background-color 0.2s;
      cursor: pointer;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      width: 25%;
      @media only screen and (max-width: 800px) {
        width: 50%;
      }
      @media only screen and (max-width: 400px) {
        width: 100%;
      }
      &:hover {
        background-color: $c08;
        & .eqftp-title {
          color: $c01;
        }
        & .eqftp__col_buttons {
          display: flex;
        }
      }
      & .eqftp-title {
        padding-left: 0;
        color: $c09;
        & .material-icons {
          margin-left: $i2;
          font-size: 10px;
          color: $c06;
        }
      }
      &_new {
        justify-content: center;
        align-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        width: 100%;
      }
      &_idle {
        & .eqftp-title {
          color: $c09;
          & .material-icons {
            color: $c06;
          }
        }
      }
      &_connect {
        & .eqftp-title {
          color: $c01;
          & .material-icons {
            color: $green;
            animation-name: flash;
            animation-duration: 5s;
            animation-iteration-count: infinite;
          }
        }
      }
      &_error {
        & .eqftp-title {
          color: $c06;
          & .material-icons {
            color: $red;
            animation-name: flash;
            animation-duration: 5s;
            animation-iteration-count: infinite;
          }
        }
      }
    }
  }
}
